.formInput{display:flex;flex-direction:column}.formInput label{font-size:14px;font-weight:600;color:#505f6f;margin-top:4px;margin-bottom:4px}.formInput input{outline:none;border:1px solid #f5f7fb;padding:8px;border-radius:5px}.formInput select{outline:none;border:1px solid #f5f7fb;border-radius:5px;padding:8px}.formInput input:focus{border:1px solid #0094c6}.saveButton{width:100%;background-color:#0094c6;color:#fff;box-shadow:none;padding:4px 8px;border:none;margin-top:8px;border-radius:5px}.saveButton:hover{background-color:rgba(0,148,198,.8)}